import { Component, OnInit } from "@angular/core";
import { Location } from "@angular/common";
import { ActivatedRoute, Router } from "@angular/router";
import { DashboardmoduleService } from "../../dashboardmodule.service";
import { environment } from "../../../../../environments/environment";
import { ToastrService } from "ngx-toastr";
declare var $;

@Component({
	selector: "app-application-individual",
	templateUrl: "./application-individual.component.html",
	styleUrls: ["./application-individual.component.css"],
})
export class ApplicationIndividualComponent implements OnInit {
	isLoading: boolean;
	applicationData: any;
	applicationId: any;
	server_url: string = environment.server_url;
	constructor(private _sol: DashboardmoduleService, private route: ActivatedRoute, private location: Location, private router: Router, private toastr: ToastrService) {
		this.route.params.subscribe((params) => {
			this.applicationId = params.id;
			// console.log(this.applicationId);
		});
	}

	ngOnInit() {
		this.fetchApplication();
	}

	goBack() {
		this.location.back();
	}

	async fetchApplication() {
		this.isLoading = true;
		this.applicationData = await this._sol.applicationById(this.applicationId);
		// console.log(this.applicationData);
		this.isLoading = false;
	}
}
