import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Select } from "@ngxs/store";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from '../../../../state/model/loggedinuser.model';
import { DashboardmoduleService } from '../../dashboardmodule.service';

@Component({
  selector: 'app-unsolicited-proposal-provider',
  templateUrl: './unsolicited-proposal-provider.component.html',
  styleUrls: ['./unsolicited-proposal-provider.component.css']
})
export class UnsolicitedProposalProviderComponent implements OnInit {

  @Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
  uns_proposal: any;
  query: any;
  post_data:any ={
    "filter": {},
    "pg": {
      limit: 10,
      skip: 0
    }
  };

  constructor(private _ds:DashboardmoduleService,private router:Router) {
    this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
  }

  ngOnInit() {
    // console.log(this.loggedInUser)
    this.getUnsolicitedProposals()
  }

  async getUnsolicitedProposals(){
    this.post_data.filter.frg_provider_id = this.loggedInUser.userTypeId;
    let resp:any = await this._ds.getUnsolicitedChallenges(this.post_data);
    if(resp.data.length){
      this.uns_proposal = resp
    }
  }

  handlePagination(pageNumber: number) {
    this.post_data.pg.skip = (pageNumber - 1) * this.post_data.pg.limit;
		this.getUnsolicitedProposals();
	}

}
