import { Component, OnInit } from "@angular/core";
import { DashboardmoduleService } from "../../dashboardmodule.service";

@Component({
	selector: "app-eois",
	templateUrl: "./eois.component.html",
	styleUrls: ["./eois.component.css"],
})
export class EoisComponent implements OnInit {
	isLoading: boolean;
	eoiData: any;
	query: any = {
		filter: {
			status: "active",
			proposal_received: false,
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	constructor(private _app: DashboardmoduleService) {}

	ngOnInit() {
		this.fetchEois();
	}

	async fetchEois() {
		this.isLoading = true;
		this.eoiData = await this._app.getEois(this.query);
		this.isLoading = false;
	}
}
