import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Injectable({
	providedIn: "root",
})
export class DashboardmoduleService {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	api_url = environment.api_url;
	constructor(private http: HttpClient) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});
	}

	async getAllApplications(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + `/application/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/application/count`, query).toPromise();
		await Promise.all(
			dataResponse.data.map(async (a) => {
				console.log(a.frg_anchor_id,a.id)
				let seekerResp: any = await this.http
					.post(this.api_url + `/seekers/select_slug`, {
						slug: {
							id: a.frg_anchor_id.frg_seeker_id,
						},
					})
					.toPromise();
				a.frg_anchor_id.frg_seeker_id = seekerResp.data;

				if (a.eoi && a.eoi[0]) {
					let eoiResp: any = await this.http
						.post(this.api_url + `/eoi/select`, {
							slug: {
								id: a.eoi[0].id,
							},
						})
						.toPromise();
					a.eoi[0] = eoiResp.data;
				}
			})
		);
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getApplications(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + `/application/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/application/count`, query).toPromise();
		await Promise.all(
			dataResponse.data.map(async (a) => {
				let seekerResp: any = await this.http
					.post(this.api_url + `/seekers/select_slug`, {
						slug: {
							id: a.frg_anchor_id.frg_seeker_id,
						},
					})
					.toPromise();
				a.frg_anchor_id.frg_seeker_id = seekerResp.data;
			})
		);
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getProposals(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + `/proposal/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/proposal/count`, query).toPromise();

		await Promise.all(
			dataResponse.data.map(async (p) => {
				let challengeResp: any = await this.http
					.post(this.api_url + `/challenges/select`, {
						slug: {
							id: p.frg_anchor_id.frg_challenge_id,
						},
					})
					.toPromise();
				p.frg_anchor_id.frg_challenge_id = challengeResp.data;
				let seekerResp: any = await this.http
					.post(this.api_url + `/seekers/select_slug`, {
						slug: {
							id: p.frg_anchor_id.frg_seeker_id,
						},
					})
					.toPromise();
				p.frg_anchor_id.frg_seeker_id = seekerResp.data;

				let applicationResp: any = await this.http
					.post(this.api_url + `/application/select`, {
						slug: {
							id: p.frg_eoi_id.frg_application_id,
						},
					})
					.toPromise();
				p.frg_eoi_id.frg_application_id = applicationResp.data;
			})
		);
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async deleteApplication(solutionId: number) {
		return await this.http
			.post(this.api_url + "/application/delete", {
				slug: { id: solutionId },
			})
			.toPromise();
	}

	async applicationById(applicationId: number) {
		return await this.http
			.post(this.api_url + `/application/select`, {
				slug: {
					id: applicationId,
				},
			})
			.toPromise();
	}

	async fetchproposalById(proposalId: number) {
		return await this.http
			.post(this.api_url + `/proposal/select`, {
				slug: {
					id: proposalId,
				},
			})
			.toPromise();
	}

	async updateApplication(postData) {
		return await this.http.post(this.api_url + `/application/update`, postData).toPromise();
	}

	async updateEOI(postData) {
		return await this.http.post(this.api_url + `/eoi/update`, postData).toPromise();
	}

	async updateProposal(postData) {
		return await this.http.post(this.api_url + `/proposal/update`, postData).toPromise();
	}

	async removeFromProductImages(data) {
		return await this.http.post(this.api_url + "/application/remove_from_images", data).toPromise();
	}

	async addtoProductImages(data) {
		return await this.http.post(this.api_url + "/application/add_to_images", data).toPromise();
	}

	async uploadSolutionImages(formData) {
		return await this.http.post(this.api_url + "/application/uploads", formData).toPromise();
	}

	async getEois(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		let userApplications: any = await this.http
			.post(this.api_url + `/application/select_mul`, {
				filter: {
					frg_provider_id: this.loggedInUser.userTypeId,
				},
			})
			.toPromise();
		let eoiApplicationIds = userApplications.data.map((app) => app.id);
		query.filter.frg_application_id = eoiApplicationIds;
		const dataResponse: any = await this.http.post(this.api_url + `/eoi/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/eoi/count`, query).toPromise();
		await Promise.all(
			dataResponse.data.map(async (eoi) => {
				let challengeResp: any = await this.http
					.post(this.api_url + `/challenges/select`, {
						slug: {
							id: eoi.frg_application_id.frg_challenge_id,
						},
					})
					.toPromise();
				eoi.frg_application_id.frg_challenge_id = challengeResp.data;
				let cityResp: any = await this.http
					.post(this.api_url + `/smart_cities/select`, {
						slug: {
							id: eoi.frg_seeker_id.frg_smart_city_id,
						},
					})
					.toPromise();
				eoi.frg_seeker_id.frg_smart_city_id = cityResp.data;
			})
		);
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async uploadPastPilotdocs(formData) {
		return await this.http.post(this.api_url + "/proposal/upload_pilot_docs", formData).toPromise();
	}

	async uploadAdditionaldocs(formData) {
		return await this.http.post(this.api_url + "/proposal/upload_additional_docs", formData).toPromise();
	}

	async addToPastPilotdocs(formData) {
		return await this.http.post(this.api_url + "/proposal/addto_pilot_docs", formData).toPromise();
	}

	async addToAdditionaldocs(formData) {
		return await this.http.post(this.api_url + "/proposal/addto_additional_docs", formData).toPromise();
	}

	async removePastPilotDocs(postData) {
		return await this.http.post(this.api_url + "/proposal/remove_pilot_docs", postData).toPromise();
	}

	async removeAdditionalDocs(postData) {
		return await this.http.post(this.api_url + "/proposal/remove_additional_docs", postData).toPromise();
	}

	async newProposal(postData) {
		return await this.http.post(this.api_url + "/proposal/create", postData).toPromise();
	}

	async getUnsolicitedChallenges(post_data:any){
		const limit = post_data.pg.limit;
    	const page = post_data.pg.skip / limit + 1;

		let resp_data:any = await this.http.post(this.api_url + '/unsolicited_proposal/select_mul',post_data).toPromise();
		let totalResults: any = await this.http .post(this.api_url + "/unsolicited_proposal/count", post_data).toPromise();

		let startItem = totalResults.data > 0 ? (page - 1) * limit + 1 : 0;
		let endItem =
			totalResults.data > 0 ? startItem - 1 + resp_data.data.length : 0;
		let PageCount = Math.ceil(totalResults.data / limit);
		resp_data.currently_showing = { from: startItem, to: endItem };
		resp_data.total_results = totalResults.data;
		resp_data.page_count = PageCount;
		return resp_data
	}

}
