import { Component, Input, OnInit } from "@angular/core";
import { environment } from "../../../../../environments/environment";
import { Router, ActivatedRoute } from "@angular/router";
declare var $;
@Component({
	selector: "proposal-card",
	templateUrl: "./proposal-card.component.html",
	styleUrls: ["./proposal-card.component.css"],
})
export class ProposalCardComponent implements OnInit {
	@Input() proposalData: any;
	serverUrl: string = environment.server_url;
	constructor(private route: ActivatedRoute, private router: Router) {}

	ngOnInit() {}

	extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}

	editProposal() {
		$(`#proposalModal_${this.proposalData.id}`).modal("hide");
		this.router.navigate([`${this.proposalData.id}/edit`], { relativeTo: this.route });
	}
}
