import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { DashboardmainService } from "src/app/seeker/challenge-dashboard/dashboardmain.service";
import { environment } from "../../../../../environments/environment";
@Component({
	selector: "pilot-card",
	templateUrl: "./pilot-card.component.html",
	styleUrls: ["./pilot-card.component.css"],
})
export class PilotCardComponent implements OnInit {
	@Input() pilotData: any;
	@Output() onStatusChange = new EventEmitter<any>();
	serverUrl: string = environment.server_url;
	statusUpdating: boolean;
	constructor(private _dash: DashboardmainService, private toastr: ToastrService) {}

	ngOnInit() {
		// console.log(this.pilotData);
	}

	extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}
}
