import { Component, OnInit } from "@angular/core";
import { DashboardmoduleService } from "../../dashboardmodule.service";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-proposals",
	templateUrl: "./proposals.component.html",
	styleUrls: ["./proposals.component.css"],
})
export class ProposalsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	isLoading: boolean;
	allProposals: any;
	pilotReadyProposals: any;
	prePilotProposals: any;
	allProposalCount: number = 0;
	prePilotCount: number = 0;
	pilotReadyCount: number = 0;

	queryAll: any = {
		filter: {
			status: "active",
			proposal_stage: ["New", "Pilot Ready", "Pre-Pilot"],
			pilot_status: "Yet to Start",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryPilotReady: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
			pilot_status: "Yet to Start",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryPrePilot: any = {
		filter: {
			status: "active",
			proposal_stage: "Pre-Pilot",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	constructor(private _dash: DashboardmoduleService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
			this.queryAll.filter.frg_provider_id = this.loggedInUser.userTypeId;
			this.queryPilotReady.filter.frg_provider_id = this.loggedInUser.userTypeId;
			this.queryPrePilot.filter.frg_provider_id = this.loggedInUser.userTypeId;
		});
	}

	ngOnInit() {
		this.fetchAllProposals();
	}

	async fetchAllProposals() {
		this.isLoading = true;
		this.allProposals = await this._dash.getProposals(this.queryAll);
		this.allProposalCount = this.allProposals.total_results;
		this.prePilotProposals = await this._dash.getProposals(this.queryPrePilot);
		this.prePilotCount = this.prePilotProposals.total_results;
		this.pilotReadyProposals = await this._dash.getProposals(this.queryPilotReady);
		this.pilotReadyCount = this.pilotReadyProposals.total_results;
		this.isLoading = false;
	}

	handleAllPagination() {}
}
