import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { environment } from "../../../../../environments/environment";
import { DashboardmoduleService } from "../../dashboardmodule.service";
import { ToastrService } from "ngx-toastr";
declare var $;

@Component({
	selector: "application-item",
	templateUrl: "./application-item.component.html",
	styleUrls: ["./application-item.component.css"],
})
export class ApplicationItemComponent implements OnInit {
	@Input() applicationData;
	@Output() onApplicationDelete = new EventEmitter<any>();
	serverUrl: string = environment.server_url;
	deleteLoading: boolean;
	constructor(private _dash: DashboardmoduleService, private toastr: ToastrService) {}

	ngOnInit() {}

	async trashApplication() {
		this.deleteLoading = true;
		let resp: any = await this._dash.deleteApplication(this.applicationData.id);
		$(`#deleteSolution${this.applicationData.id}`).modal("hide");
		this.toastr.success("", "Application deleted successfully");
		this.onApplicationDelete.emit(resp);
		this.deleteLoading = false;
	}
}
