import { Component, OnInit } from "@angular/core";
import { DashboardmoduleService } from "../../dashboardmodule.service";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

@Component({
	selector: "app-all-applications",
	templateUrl: "./all-applications.component.html",
	styleUrls: ["./all-applications.component.css"],
})
export class AllApplicationsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	isLoading: boolean;
	applicationDataAll: any;

	query: any = {
		filter: {
			status: "active",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	constructor(private _dash: DashboardmoduleService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
			this.query.filter.frg_provider_id = this.loggedInUser.userTypeId;
		});
	}

	ngOnInit() {
		this.fetchAllApplications();
	}

	async fetchAllApplications() {
		this.isLoading = true;
		this.applicationDataAll = await this._dash.getAllApplications(this.query);
		this.applicationDataAll.data.map((a) => {
			if (a.application_status === "New") {
				a.overall_stage = "Application in Review";
			} else {
				a.overall_stage = a.application_status;
			}
			if (a.eoi && a.eoi[0]) {
				if (a.eoi[0].proposal_received) {
					if(a.eoi[0].proposals.length){
						if (a.eoi[0].proposals[0].proposal_stage === "New") {
							a.overall_stage = "Proposal in Review";
						} else {
							a.overall_stage = a.eoi[0].proposals[0].proposal_stage;
						}
					}
					
				}
			}
		});
		this.isLoading = false;
	}

	handleDeleteApplication() {
		this.fetchAllApplications();
	}

	handlePagination(pageIndex) {
		this.query.pg.skip = (pageIndex - 1) * this.query.pg.limit;
		this.fetchAllApplications();
	}
}
