import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { PermissionGuard } from "../../permission.guard";
import { DashboardComponent } from "./dashboard.component";
import { AllApplicationsComponent } from "./screens/all-applications/all-applications.component";
import { ApplicationsComponent } from "./screens/applications/applications.component";
import { EoisComponent } from "./screens/eois/eois.component";
import { PilotsComponent } from "./screens/pilots/pilots.component";
import { ProposalsComponent } from "./screens/proposals/proposals.component";
import { PilotProposalComponent } from "./screens/pilot-proposal/pilot-proposal.component";
import { ApplicationIndividualComponent } from "./screens/application-individual/application-individual.component";
import { EditApplicationComponent } from "./screens/edit-application/edit-application.component";
import { EditProposalComponent } from "./screens/edit-proposal/edit-proposal.component";
import { UnsolicitedProposalProviderComponent } from "./screens/unsolicited-proposal-provider/unsolicited-proposal-provider.component";
const routes: Routes = [
	{
		path: "",
		component: DashboardComponent,
		children: [
			{
				path: "",
				redirectTo: "all-applications",
				pathMatch: "full",
			},
			{
				path: "all-applications",
				component: AllApplicationsComponent,
			},
			{
				path: "all-applications/:id/edit",
				component: EditApplicationComponent,
			},
			{
				path: "all-applications/:id",
				component: ApplicationIndividualComponent,
			},
			{
				path: "applications",
				component: ApplicationsComponent,
			},
			{
				path: "applications/:id",
				component: ApplicationIndividualComponent,
			},
			{
				path: "applications/:id/edit",
				component: EditApplicationComponent,
			},
			{
				path: "eois",
				component: EoisComponent,
			},
			{
				path: "eois/create_proposal",
				component: PilotProposalComponent,
			},
			{
				path: "pilots",
				component: PilotsComponent,
			},
			{
				path: "proposals",
				component: ProposalsComponent,
			},
			{
				path: "proposals/:id/edit",
				component: EditProposalComponent,
			},
			{
				path: "unsolicited-proposals",
				component: UnsolicitedProposalProviderComponent,
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class DashboardRoutingModule {}
