import { Component, OnInit } from "@angular/core";
import { DashboardmoduleService } from "../../dashboardmodule.service";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
@Component({
	selector: "app-applications",
	templateUrl: "./applications.component.html",
	styleUrls: ["./applications.component.css"],
})
export class ApplicationsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	isLoading: boolean;
	applicationDataAll: any;
	applicationDataRejected: any;
	applicationDataonHold: any;
	allApplicationCount: number = 0;
	RejectCount: number = 0;
	onHoldCount: number = 0;
	challengeAnchorId: any;
	queryAll: any = {
		filter: {
			status: "active",
			application_status: "New",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryRejected: any = {
		filter: {
			status: "active",
			application_status: "Reject",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryHold: any = {
		filter: {
			status: "active",
			application_status: "Onhold",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	constructor(private _dash: DashboardmoduleService) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
			this.queryAll.filter.frg_provider_id = this.loggedInUser.userTypeId;
			this.queryRejected.filter.frg_provider_id = this.loggedInUser.userTypeId;
			this.queryHold.filter.frg_provider_id = this.loggedInUser.userTypeId;
		});
	}

	ngOnInit() {
		this.fetchAllApplications();
	}

	async fetchAllApplications() {
		this.isLoading = true;
		this.applicationDataAll = await this._dash.getApplications(this.queryAll);
		this.allApplicationCount = this.applicationDataAll.total_results;
		this.applicationDataRejected = await this._dash.getApplications(this.queryRejected);
		this.RejectCount = this.applicationDataRejected.total_results;
		this.applicationDataonHold = await this._dash.getApplications(this.queryHold);
		this.onHoldCount = this.applicationDataonHold.total_results;
		this.isLoading = false;
	}

	handleDelete(deletedApplication) {
		this.fetchAllApplications();
	}
}
